<template>
      <ul>
        <li class="menu-item-list" v-for="(val, index) in currentSubmenu" v-on:click="selectSubmenuItem(index)">
          <span class="menu-item-text">{{ val }}</span>
        </li>
      </ul>
</template>

<script type="text/ecmascript-6">
  var itemValType = ['创业','公益','亲子','科技','运动','互联网','音乐','交友']
  var itemValTime =['全时段','今天','明天','本周','本周末','本月']
  var itemValPrice = ['全价位','免费','收费']

  export default {
      data (){
          return {
              currentId: 0,
              currentSubmenu: itemValType,
              submenuType: {
                itemId: 0,
                itemVal: itemValType
              },
              submenuTime: {
                itemId: 1,
                itemVal: itemValTime
              },
              submenuPrice: {
                itemId: 2,
                itemVal:itemValPrice
              },
              beSelectedSubmenuItem: ''
        }
      },
      watch:{
          currentId:{
            handler(curVal,oldVal){
                //监听当前的菜单Id是否改变，是则改变currentArray里面的值
//                console.log(curVal) //打印选择的下标
                  if (curVal===this.submenuType.itemId){
                      this.exchangeArray(this.submenuType)
                  }
                  else if(curVal===this.submenuTime.itemId){
                      this.exchangeArray(this.submenuTime)
                  }
                  else if(curVal===this.submenuPrice.itemId){
                      this.exchangeArray(this.submenuPrice)
                  }
            }

          }
      },
      methods: {
        /**
         * 替换数组的值
         * @param beStoreArray  要被存储的数组
         */
          exchangeArray: function(beStoreArray){
              this.currentSubmenu = []
              this.currentSubmenu = beStoreArray.itemVal
          },
        /**
         * 选择子菜单的值，并将所选择的值传输给menus-select组件
         * @param index
         */
          selectSubmenuItem: function(index){
              this.beSelectedSubmenuItem = this.currentSubmenu[index]
              this.$emit('selectedSubmenuItem',this.beSelectedSubmenuItem)
              this.$parent.isShow = false;  //点击item后关掉parent
              //选择的值
              //触发组件discovery.vue的事件
          }
      },
  }
</script>

<style lang="stylus" rel="stylesheet/stylus">
  .menu-item
    background-color : #fff
    width: 100%;
    .menu-item-list
      padding: 12px
      border-bottom: 1px solid #eee
      color: #787871
</style>



