<template>
  <div class="content">
    <div class="left">
      <img :src="activity.img">
    </div>
    <div class="right">
      <p class="right-title">{{ activity_title }}</p>
      <p class="right-location">{{ locationtext }}</p>
      <p class="right-time">{{ activity.time }}</p>
      <p class="right-price">{{ activity.price }}</p>
    </div>
  </div>


</template>

<script type="text/ecmascript-6">
  export default {
    props: {
      activity: {
        type: Object
      },
    },
    computed: {
      locationtext() {
        return `${this.activity.location.slice(0, 20)}...`;
      },
      activity_title() {
        return `${this.activity.title.slice(0, 28)}……`;
      }
    },

  }


</script>

<style lang="stylus" rel="stylesheet/stylus">
  @import '../../assets/style/minxin.styl'

  .content
    display: flex
    justify-content: center
    width: 100%
    height: 85px
    background-color: #fafafa
    border-1px(#eee)
    .left
      flex: 0 0 100px
      height: 120px
      img
        padding: 10px
        width: 110px
        height: 65px
    .right
      position: relative
      display: inline-block
      flex: 1
      font-size: 0
      height: 100%
      padding-right: 5px
      .right-title
        display: block
        font-size: 14px
        font-weight: 500
        margin-top: 10px
        color: #323232
      .right-location
        display: inline-block
        width: 20em
        margin-top: 5px
        font-size: 12px
        color: #8f8f8f
      .right-time
        position: absolute
        left: 0
        bottom: 10px
        font-size: 12px
        color: #8f8f8f
      .right-price
        position: absolute
        margin-top: 5px
        font-size: 10px
        color: green
        right: 0
        bottom: 10px
        margin-right: 10px

  /*  !*
    overflow: hidden; !*自动隐藏文字*!
    text-overflow: ellipsis;!*文字隐藏后添加省略号*!
    white-space: nowrap;!*强制不换行*!
    *!*/

</style>



