<template>
  <div id="recommend">

      <div class="content-wrapper" v-show="showFlag">
        <Slider :items="imgs" :width="560" :height="160" :sync="true" class="silder"></Slider>

        <div class="category" border-1px>
          <a class="category-item">
            <div class="category-item-icon"><img src="./news.png" alt=""></div>
            <span class="category-item-text">最新</span>
          </a>
          <a class="category-item">
            <div class="category-item-icon"><img src="./hot.png" alt=""></div>
            <span class="category-item-text">热门</span>
          </a>
          <a class="category-item">
            <div class="category-item-icon"><img src="./nearby.png" alt=""></div>
            <span class="category-item-text">附近</span>
          </a>
          <a class="category-item">
            <div class="category-item-icon"><img src="./calendar.png" alt=""></div>
            <span class="category-item-text">日历</span>
          </a>
        </div>
        <div class="subject">
          <div class="subject-title">
            <img class="subject-title-icon" src="./star.png"></img>
            <span class="subject-title-text">专题精选</span>
          </div>
          <div class="subject-img">
            <img src="./subject.png" alt="">
          </div>
        </div>
        <div class="recommend-wrapper">
          <div class="recommend-header-wrapper">
            <img src="./fine.png" alt="" class="recommend-header-icon">
            <span class="recommend-header-text">强力推荐</span>
          </div>
          <div class="item-list">
            <ul>
              <li v-for=" item in activities" :key="item.title" @click="selectItem(item,$event)">
                <v-item :activity="item" ></v-item>
              </li>
            </ul>
            <infinite-loading :on-infinite="onInfinite" ref="infiniteLoading">
             <span slot="no-more">
                没有更多的消息了 :(
             </span>
            </infinite-loading>
          </div>
        </div>
      </div>

    <transition name="fade">
      <item-detail :activity="selectedItem" ref="detail" v-show="!showFlag"></item-detail>
    </transition>
  </div>
</template>


<script type="text/javascript">
import vHeader from "../header/header";
import Slider from "../slider/slider"; //banner
import Vitem from "../Item/item";
import InfiniteLoading from "vue-infinite-loading";
import itemDetail from "../item-detail/item-detail"; //详情页组件

const ERROR_OK = 0;

// 数据
let imgs = [
  {
    href: "",
    src: "http://wimg.huodongxing.com/Content/app/appom/642673446017359.jpg"
  },
  {
    href: "",
    src: "http://wimg.huodongxing.com/Content/app/appom/482684639696037.jpeg"
  },
  {
    href: "",
    src: "http://wimg.huodongxing.com/Content/app/appom/692680412987103.jpg"
  },
  {
    href: "",
    src: "http://wimg.huodongxing.com/Content/app/appom/742674485900047.jpeg"
  },
  {
    href: "",
    src: "http://wimg.huodongxing.com/Content/app/appom/562639727377922.jpg"
  }
];
export default {
  name: "recommend",
  data() {
    return {
      imgs: imgs,
      posts: [],
      activities: [],
      temp_activities: [],
      selectedItem: {},
      showFlag: true
    };
  },
  created() {
    const appData = require("../../../static/data.json");

    this.temp_activities = appData.data;
    this.activities = this.temp_activities.slice(0, 12);

  /*   this.$http.get("/api/posts").then(response => {
      response = response.body;

      if (response.errno === ERROR_OK) {
        this.$nextTick(() => {
          this.temp_activities = response.posts;
          this.activities = this.temp_activities.slice(0, 12);
        });
      }
    }); */
  },
  components: {
    "v-item": Vitem,
    Slider,
    InfiniteLoading,
    "item-detail": itemDetail
  },
  methods: {
    onInfinite() {
      setTimeout(() => {
        const temp = [];
        for (
          let i = this.activities.length + 1;
          i <= this.activities.length + 10;
          i++
        ) {
          temp.push(this.temp_activities[i]);
        }
        this.activities = this.activities.concat(temp);
        this.$refs.infiniteLoading.$emit("$InfiniteLoading:loaded");
      }, 1000);
    },
    selectItem(item, event) {
      this.selectedItem = item;
      this.$refs.detail.showDetail();
      this.showFlag = false;
    }
  }
};
</script>


<style lang="stylus" rel="stylesheet/stylus">
@import '../../assets/style/minxin.styl';

#recommend {
  padding-top: 50px;

  .slider {
    /* 类型 */
  }

  .category {
    display: flex;
    width: 100%;
    height: 70px;
    background-color: #fff;
    overflow: hidden;
    border-1px(rgba(7, 17, 27, 0.1));

    .category-item {
      flex: 1;
      position: relative;
      text-align: center;
    }

    .category-item-icon {
      display: block;
      margin-top: 8px;
      width: 100%;

      img {
        display: inline-block;
        height: 35px;
        width: 35px;
      }
    }

    .category-item-text {
      display: inline-block;
      margin-top: 5px;
      bottom: 0;
      font-size: 12px;
      color: rgba(0, 0, 0, 0.4);
    }
  }

  /* 专题 */
  .subject {
    border-top: 10px solid #eee;
    border-bottom: 10px solid #eee;
    padding: 8px;
    background: #fff;
    border-1px(rgba(7, 17, 27, 0.1));

    .subject-title {
      display: block;
      font-size: 0;
      height: 15px;

      .subject-title-icon {
        vertical-align: top;
        height: 14px;
        width: 14px;
      }

      .subject-title-text {
        line-height: 15px;
        margin-left: 10px;
        font-size: 14px;
        color: #000;
      }
    }

    .subject-img {
      margin-top: 10px;

      img {
        width: 100%;
        height: 160px;
      }
    }
  }

  .recommend-wrapper {
    margin-bottom: 52px;

    .recommend-header-wrapper {
      display: block;
      height: 20px;
      font-size: 0;
      padding: 10px 0 10px 10px;
      border-1px(rgba(7, 17, 27, 0.1));

      .recommend-header-icon {
        display: inline-block;
        width: 18px;
        vertical-align: top;
      }

      .recommend-header-text {
        display: inline-block;
        margin-left: 10px;
        line-height: 20px;
        font-size: 16px;
      }
    }
  }
}
</style>



