<template>
  <div class="detail-wrapper" v-show="showFlag">
    <div class="header-wrapper">
      <span class="back-icon icon-arrow_lift" @click="close"></span>
      <div class="header-icon">
        <img class="header-img" src="./logo.png" alt="">
      </div>
    </div>
    <div class="banner-wrapper">
      <!--<img class="banner-img" :src="activity.detail.banner" alt="">-->
    </div>
    <div class="title-wrapper">
      <span class="title">      {{ activity.title }}</span>
      <div class="user-wrapper">
        <div class="image-wrapper">
          <img class="user-image" :src="activity.detail.user_img" alt="">
        </div>
        <div class="user-name">
          {{activity.detail.user_name}}
        </div>
      </div>
    </div>
    <div class="time-wrapper">
      <div class="icon"></div>
      <div class="text">
        {{ activity.time }}
      </div>
    </div>
    <div class="localtion-wrapper">
