      <div class="icon"></div>
      <div class="text">
        {{ price }}
      </div>
    </div>
  </div>
</template>


<script type="text/javascript">
  export default {
    props: {
      activity: {
        type: Object
      }
    },
    data() {
      return {
        showFlag: false
      }
    },
    methods: {
      showDetail(){
//        this.showFlag = true;
      },
      close() {
//        this.showFlag = false;
        this.$parent.showFlag = true;
      }
    },
   computed: {
     price() {
       return this.activity.detail.real_price === 0 ? '免费' : this.activity.detail.real_price;
     }
   }
  }
</script>


