<template>
  <div class="header_wrapper" v-show="show">
    <div class="adderss-wrapper" @click="m_showAddress" v-show="showDetail">
      <span class="address-text">{{ city }}</span>
      <img src="./drop-down.png" alt="">
    </div>
    <div class="header-title" >
      <img src="./logo.png" alt="">
    </div>
    <div class="header-search" @click="m_showSearch">
      <img src="./lookup.png" alt="" v-show="showDetail">
    </div>

    <transition name="slideRight">
      <v-address v-show="showAddress" ></v-address>
    </transition>

    <transition name="slideRight">
      <v-search v-show="showSearch"></v-search>
    </transition>
  </div>
</template>


<script type="text/javascript">
  import address from './address';
  import  {bus}  from '../../bus.js';
  import BScroll from 'better-scroll';
  import search from './search';

  export default{
    data() {
      return {
        showAddress: false,
        showSearch: false
      }
    },
    props: {
      show: {
        type: Boolean,
        default: true,
      },
      showDetail: {
        type: Boolean,
        default: true,
      }
    },
    methods: {
      m_showAddress(event) {
        this.showAddress = true
        this.$parent.showApp = false
      },
      m_showSearch(event) {
        this.showSearch = true
      }
    },
    watch: {
      showAddress: function (newValue) {
        if (newValue == false) {
          bus.$emit('show', false)
        } else {
          bus.$emit('show', true)
        }
      },
      showSearch: function (newValue) {
        if (newValue == false) {
          bus.$emit('show', false)
        } else {
          bus.$emit('show', true)
        }
      }
    },
    components: {
      "v-address": address,
      'v-search': search
    },
    computed: {
      city() {
        return this.$store.state.city;
    }
  }
  }
</script>


<style lang='stylus' rel="stylesheet/stylus">
  .header_wrapper
    display: flex
    position: fixed
    top: 0
    height: 50px
    width: 100%
    font-size: 0
    border-bottom: 1px solid #7e8c8d
    background: #44BB8C
    z-index 30
    .adderss-wrapper
      position: absolute
      flex: 0 0 70px
      vertical-align: top
      text-align center
      .address-text
        line-height: 50px
        padding-left: 10px
        font-size: 16px
        color: #fff
      img
        display: inline-block
        margin-left: 2px
        height: 6px
    .header-title
      flex: 1
      display: inline-block
      margin-left: 40px
      text-align: center
      img
        height: 20px
        margin-top: 15px
    .header-search
      flex: 0 0 40px
      text-align: center
      img
        height: 20px
        margin-top: 15px
</style>



