<template>
  <div id="discovery">
      <div class="content-wrapper" v-show="showFlag" >
        <menus-select class="menu-select"></menus-select>
        <div class="item-list">
          <ul>
            <li v-for="item in activities" :key="item.title" @click="selectItem(item,$event)">
              <v-item :activity="item"></v-item>
            </li>
          </ul>
          <infinite-loading :on-infinite="onInfinite" ref="infiniteLoading">
                <span slot="no-more">
                  没有更多的消息了! :(
                </span>
          </infinite-loading>
        </div>
      </div>

    <transition name="fade">
      <item-detail :activity="selectedItem" ref="detail" v-show="!showFlag"></item-detail>
    </transition>
  </div>
</template>


<script type="text/javascript">
import { bus } from "../../bus.js"; //事件总线
import vMenuSelect from "./menus-select.vue";
import vItem from "../Item/item";
import vMenuItem from "./menu-item";
import InfiniteLoading from "vue-infinite-loading";
import Vue from "vue";
import itemDetail from "../item-detail/item-detail"; //详情页组件
const ERR_OK = 0;

export default {
  name: "discovery",
  components: {
    "v-item": vItem,
    InfiniteLoading,
    "menus-select": vMenuSelect,
    "menu-item": vMenuItem,
    "item-detail": itemDetail
  },
  data() {
    return {
      activities: [],
      temp_activities: [],
      menusId: 0,
      type: "全价位",
      selectedItem: {},
      showFlag: true
    };
  },
  created() {
    const appData = require("../../../static/data.json");
    this.temp_activities = appData.data;
    this.activities = this.temp_activities.slice(0, 12);
    /*       var appData = require('../static/data.json')
      this.$http.get('/api/posts').then((response) => {
        response = response.body
        if (response.errno === ERR_OK) {
          this.$nextTick(() => {
            this.temp_activities = response.posts
            this.activities = this.temp_activities.slice(0,12)
          })
        }
      }); */

    //监听改掉价位
    bus.$on("type-selected", text => {
      this.type = text;

      this.activities = [];

      this.onInfinite();
    });
  },
  methods: {
    onInfinite() {
      setTimeout(() => {
        const temp = [];
        let l = 0;
        for (let i = this.activities.length; l <= 12; i++) {
          //全价位
          if (this.type == "全价位") {
            temp.push(this.temp_activities[i]);
            //              console.log(`${this.activities.length} + ${i}`)
            l++;
            continue;
          }

          //筛选
          if (this.type == "免费") {
            if (this.type === this.temp_activities[i].price.trim()) {
              temp.push(this.temp_activities[i]);
              l++;
            }
          } else if (this.temp_activities[i].price.trim() != "免费") {
            temp.push(this.temp_activities[i]);
            l++;
          }
        }
        this.activities = this.activities.concat(temp);
        this.$refs.infiniteLoading.$emit("$InfiniteLoading:loaded");
      }, 1000);
    },
    selectItem(item, event) {
      this.selectedItem = item;
      this.$refs.detail.showDetail(); //调用子组件的方法 显示页面子组件的方法
      this.showFlag = false;
    }
  },
  computed: {
    Type() {
      return this.type;
    }
  }
};
</script>


<style lang="stylus" rel="stylesheet/stylus">
#discovery {
  position: relative;
  background: #fff;
  padding-top: 50px;

  .menu-select {
    position: fixed;
    z-index: 31;
  }

  .menu-item {
    position: fixed;
    z-index: 31;
  }

  .item-list {
    padding-top: 40px;
    margin-bottom: 50px;
  }
}
</style>



