<template>
  <div class="menus-wrapper">
    <ul class="menus">
      <li class="menus-item" v-for="(item, index) in menus" v-on:click="selectSubmenu(index)">
        <span class="menu-text" :class="{active: (menusId === index && isShow )}" :menusId="index">{{ menus[index] }}</span>
      </li>
    </ul>
    <transition name="menu-render">
      <menu-item class="menu-item" v-show="isShow" v-on:selectedSubmenuItem="hasSelected">
      </menu-item>
    </transition>

  </div>
</template>

<script type="text/ecmascript-6">
  import vMenuItem from './menu-item.vue'
  import { bus } from '../../bus.js'

  var menusItem = ['全类型', '全时段', '全定价']

  export default {
    data (){
      return {
        menus: menusItem,
        menusId: 0,
        isShow: false,
        beReplacedMenus: menusItem
      }
    },
    components: {
      "menu-item": vMenuItem
    },
    methods: {
      selectSubmenu: function (index) {
        //当点击菜单为同一个时，让子菜单消失；当点击菜单不同时，切换子菜单
        if (this.menusId === index) {
          this.isShow = !this.isShow
        } else {
          this.isShow = true
        }
//        console.log(this.isShow)
        //当点击菜单时，将菜单的index值传给menusId
        this.menusId = index
      },
      hasSelected: function (currentMenu) {
        //接收当前选择的子菜单列表值
        this.beReplacedMenus[this.menusId] = currentMenu
//        console.log(currentMenu+';')
        bus.$emit('type-selected',currentMenu)
//        this.$store.commit('Change_Type',currentMenu)
        //初始化
        this.menus = []
        this.menus = this.beReplacedMenus
      }
    }
    ,
    watch: {
      menusId: {
        handler(curVal, oldVal){
          //监听menusId的改变,相当于监听index的改变
          this.$children[0].currentId = curVal
        }
      }
    },
    events: {
        'shutdownMenuItem'(){
            this.isShow = false;
        }
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus">
  .menus-wrapper
    position: fixed
    width: 100%
    height: 40px
    background-color: #fff
    .menus
      display: flex
      justify-content: space-between
      .menus-item
        flex: 1
        text-align: center
        line-height: 40px
        .menu-text
          font-size: 15px
          color: #787871
        .active
          color: #42bd56
    .menu-item
      &.menu-render-enter-active
      &.menu-render-leave-active
        transition: opacity .3s linear
      &.menu-render-enter, &.menu-render-leave-active
        opacity: 0
</style>



