<template>
<!-- background-size: 400px 400px; -->
  <div class="mine-wrapper" >
    <div class="header-wrapper" :style="{background: `url(${require('./bg.jpg')})`}">
      <div class="setting-icon">
        <img src="./setUp.png" alt="">
      </div>
      <div class="user-image">
        <img
          src="./timg.jpg"
          alt="">
      </div>
      <div class="header-text-wrapper">
        <span class="header-text">我就是这样一个人。</span>
      </div>
    </div>
    <div class="content-wrapper">
      <mine-item v-for="(item) in item_data" :pic="item.pic" :text="item.text" :key="item.text  "></mine-item>
    </div>
  </div>
</template>


<script type="text/javascript">
import vitem from "./mine-item";

// let item_text = ['餐票','收藏的活动','关注主办方','同伴']

export default {
  data() {
    return {
      item_data: [
        {
          text: "餐票",
          pic: require("./ticket.png")
        },
        {
          text: "收藏的活动",
          pic: require("./collection.png")
        },
        {
          text: "关注主办方",
          pic: require("./follow.png")
        },
        {
          text: "同伴",
          pic: require("./companion.png")
        }
      ]
    };
  },
  methods: {},
  components: {
    "mine-item": vitem
  }
};
</script>


<style lang="stylus" rel="stylesheet/stylus">
.mine-wrapper {
  .header-wrapper {
    position: relative;
    height: 240px;
    width: 100%;


    .setting-icon {
      position: absolute;
      right: 0;
      top: 0;
      margin: 5px 5px 0 0;
      padding: 5px;

      img {
        height: 20px;
        width: 20px;
      }
    }

    .user-image {
      display: inline-block;
      position: absolute;
      text-align: center;
      width: 100%;
      height: 100%;
      margin-top: 50px;

      img {
        height: 100px;
        border-radius: 50%;
      }
    }

    .header-text-wrapper {
      display: inline-block;
      position: absolute;
      bottom: 0;
      height: 12px;
      width: 100%;
      text-align: center;
      margin-bottom: 20px;

      /* opacity: 0.8 */
      .header-text {
        display: inline-block;
        font-size: 14px;
        color: #eee;
      }
    }
  }

  .content-wrapper {
    height: 200px;
  }
}
</style>



