<template>
  <div id="message">
    <div class="message-text"><span>您还没有加入任何活动群</span></div>
  </div>
</template>


<script type="text/javascript">
  import vHeader from '../header/header'
  export default{
    name: 'message',
    components: {
      "v-header": vHeader
    }
  }
</script>


<style lang="stylus" rel="stylesheet/stylus">
  #message
    padding-top: 50px
    .message-text
      margin-top: 100px
      font-size: 20px
      text-align center

</style>



