import Vue from 'vue'
import Router from 'vue-router'
import find from '@/components/discovery/discovery'  //发现
import myself from '@/components/mine/mine'　　//我的
import recommend from '@/components/recommend/recommend'　//推荐
import message from '@/components/message/message'  //消息

Vue.use(Router)

Router.prototype.go = function () {
  this.isBack = true
  window.history.go(-1)
}

export default new Router({
  mode: 'hash',
  routes: [
    {
      path:'/',
      redirect: '/recommend'
    },
    {
      path: '/discovery',
      name: 'find',
      component: find
    },
    {
      path: '/mine',
      name: 'mine',
      component: myself
    },
    {
      path: '/recommend',
      name: 'recommend',
      component: recommend
    },
    {
      path: '/message',
      name: 'message',
      component: message
    },
  ]
})



