<template>
  <div class="item-wrapper">
    <div class="item-icon-wrapper">
      <img :src="pic"  alt="">
    </div>
    <span class="item-text">{{ text }}</span>
    <span class="right-icon icon-keyboard_arrow_right"></span>
  </div>
</template>


<script type="text/javascript">
  let icon_url = ['./ticket.png','./collection.png','./follow.png','./companion.png']
  export default{
      props:['pic','text']
  }
</script>


<style lang="stylus" rel="stylesheet/stylus">
  @import '../../../static/css/icon-style.css'
  @import '../../assets/style/minxin.styl'

  .item-wrapper
    display: flex
    font-size: 0
    width: 94%
    padding: 10px
    height: 30px
    border-1px(rgba(7, 17, 27, 0.1))
    .item-icon-wrapper
      flex: 0 0 30px
      padding: 0 5px
      margin-top: 3px
      img
        height: 20px
    .item-text
      flex 1
      font-size: 18px
      line-height: 30px
    .right-icon
      position: absolute
      flex: 0 0 20px
      display: inline-block
      font-size: 25px
      line-height: 30px
      right: 0
      color: #efeff

</style>







