<template>
  <div class="search-wrapper">
    <div class="search-header-wrapper">
      <span class="back-icon icon-arrow_lift" @click="_closeSearch"></span>
      <div class="search-input-wrapper">
        <input class="search-input" type="text" placeholder="搜索感兴趣的活动" v-focus>
        <div class="search-icon-warpper" @click="_search">
          <img class="search-icon" src="./lookup.png">
        </div>
      </div>
    </div>
    <div class="line"></div>
    <div class="search-text-wrapper">
      <div  class="text-item-wrapper" v-for="type in types">
        <span class="text">{{ type }}</span>
      </div>
    </div>
  </div>
</template>


<script type="text/javascript">
  let types = ['创业', '公益', '亲子', '科技', '运动', '互联网', '音乐', '交友', '教育',
    '校园', '电影', 'IT', '健康', '职场', '文艺', '投资', '户外', '心理', '金融', '手工', '营销',
    '旅行', '读书', '电商', '时尚', '设计', '游戏']
  export default {
    data() {
      return {
        types: types
      }
    },
    methods: {
      _search(event) {
        console.log('aa')
      },
      _closeSearch(event) {
          this.$parent.showSearch = false
      }
    },
    directives: {
      // 注册一个局部的自定义指令 v-focus
      focus: {
        // 指令的定义
        inserted: function (el) {
          // 聚焦元素
          el.focus()
        }
      }
    }
  }
</script>


<style lang="stylus" rel="stylesheet/stylus">
  @import '../../../static/css/icon-style.css'
  @import '../../assets/style/minxin.styl'

  .search-wrapper
    position: fixed
    z-index: 101
    left: 0
    top: 0
    height: 100%
    width: 100%
    overflow: hidden
    background: #fff
    .search-header-wrapper
      display: flex
      padding: 10px
      width: 100%
      height: 40px
      .back-icon
        flex: 0 0 20px
        font-size: 18px
        padding: 5px
        margin-top: 2px
    .search-input-wrapper
      flex: 1
      display: inline-block
      position: relative
      padding-left: 4px
      font-size: 0
      padding-right 30px
      .search-input
        font-size: 16px
        width: 100%
        padding: 7px
        background: #efefee
        border-radius: 15px
        outline: none
      .search-icon-warpper
        position: absolute
        right: 0
        top: 0
        padding-right 30px
        .search-icon
          padding: 5px
          height: 20px
    .line
      display: block
      height: 10px
      background: #efefee
      border-1px(rgba(7, 17, 27, 0.1))
    .search-text-wrapper
      width: 100%
      font-size: 0
      .text-item-wrapper
        display: inline-block
        height: 50px
        width: 33%
        text-align: center
        border-left: 1px solid rgba(7, 17, 27, 0.1)
        border-bottom: 1px solid rgba(7, 17, 27, 0.1)
        .text
          display: inline-block
          font-size: 16px
          padding: 10px 5px
          line-height: 30px
          color: #000

</style>







