<template>
  <div class="m-tabbar" >
    <slot></slot>
  </div>
</template>


<script type="text/javascript">
  import mTabbarItem from './tabbar-item';

  export default {
    props: ['value'],
    computed: {
//      input() {
//        this.value = this.$route.matched[0].name
//        //一个数组，包含当前路由的所有嵌套路径片段的 路由记录,这里的第一条记录就是刚刚的子组件跳转的
//      }
    }
  }
</script>


<style lang="stylus" rel="stylesheet/stylus">
  //用绝对定位固定再页面底部。
  .m-tabbar
    display: flex
    flex-direction: row
    position: fixed
    z-index: 30
    bottom: 0
    left: 0
    right: 0
    width: 100%
    height: 50px
    overflow: hidden
    clear: both
    background: #fff
    &::before
      content: ' '
      position: absolute
      left: 0
      top: 0
      right: 0
      height: 1px
      background: #e4e4e4
      transform: scaleY(0.5)
</style>



