<template>
  <div class="address-wrapper">
    <div class="address-header" border-1px>
      <span class="back-icon icon-arrow_lift" @click="closeCity"></span>
      <span class="address-title">选择城市</span>
    </div>
    <div class="choice-city-text">当前选择城市</div>
    <div class="city-wrapper">
      <div class="city-text-wrapper">
        <span class="city-text">{{ city }}</span>
        <div class="city-icon-wrapper">
          <span class="city-icon icon-checkmark"></span>
        </div>
      </div>
    </div>
    <span class="total-city-text">全部城市</span>
    <div>
      <!--<VBar>-->
        <ul class="citys-wrapper">
          <li class="citys-item" v-for="(item,index) in citys" @click="choiceCity(index)">
            <span class="city-text">{{ item }}</span>
          </li>
        </ul>
      <!--</VBar>-->

    </div>
  </div>
</template>

<script>
//  import VBar from 'v-bar';

  export default{
    methods: {
      closeCity(){
        this.$parent.showAddress = false
      },
      choiceCity(index){ //传进来一个下标

        this.$store.commit('Change_City', this.citys[index]); //更改vuex

        this.closeCity() //关闭选择城市界面
      }
    },
    computed: {
      city() {
        return this.$store.state.city; //返回全局的city属性
      },
      citys() {
        return this.$store.state.citys;
      }
    },
    components: {
//      VBar
    }
  }
</script>


<style lang="stylus" rel="stylesheet/stylus">
  @import '../../../static/css/icon-style.css'
  @import '../../assets/style/minxin.styl'

  .address-wrapper
    position: fixed
    z-index: 101
    left: 0
    top: 0
    height: 100%
    width: 100%
    overflow: hidden
    background: #fff
    .address-header
      display: flex
      height: 40px
      line-height: 40px
      font-size: 0
      padding: 10px 0 5px 10px
      border-1px(rgba(7, 17, 27, 0.3))
      .back-icon
        display: inline-block
        flex: 0 0 20px
        line-height: 40px
        vertical-align: top
        font-size: 18px
      .address-title
        flex: 1
        text-align: center
        font-size: 18px
        font-weight: 500
    .choice-city-text
      display: inline-block
      padding-left: 10px
      height: 40px
      line-height: 40px
      font-size: 16px
      color: #7e8c8d
    .city-wrapper
      display: inline-block
      height: 60px
      width: 100%
      font-size: 0
      background: #efefee
      .city-text-wrapper
        position: relative
        display: inline-block
        height: 20px
        margin-left: 30px
        line-height: 70px
        .city-text
          font-size: 16px
          text-align: center
          line-height: 20px
          padding: 8px 16px
          background: #fff
          border-radius: 5px
        .city-icon-wrapper
          position: relative
          display: inline-block
          width: 20px
          height: 20px
          right: 10px
          top: -10px
          background: aquamarine
          border-radius: 50%
          .city-icon
            position: absolute
            display: inline-block
            font-size: 14px
            color: #fff
            padding: 5px 0 0 2px
    .total-city-text
      display: block
      font-size: 16px
      padding: 15px 0 15px 10px
      color: #7e8c8d
      background: #fff
    .citys-wrapper
      display: inline-block
      overflow-y:auto
      width: 100%
      background: #eee
      .citys-item
        display: inline-block
        width: 33%
        height: 40px
        text-align: center
        margin: 20px 0 0 0
        .city-text
          flex: 1
          display: inline-block
          padding: 8px 16px
          font-size: 15px
          background: #fff
          line-height 20px //line-height 会加上padding 的高度算
          text-align: center
          border-radius: 5px
</style>



