<template>
  <div id="app">
    <v-header :showDetail="showDetail" :show="showHeader" class="header"></v-header>

    <transition name="fadeLeft" >
      <div class="appcontent-wrapper" v-show="showApp">

          <router-view class="content-wrapper"></router-view>

        <m-tabbar v-model="select" class="tabbar-wrapper">
          <m-tabbar-item id="recommend" isRouter>
            <img src="./assets/images/recommend_off.png" alt="" slot="icon-normal">
            <img src="./assets/images/recommend_on.png" alt="" slot="icon-active">
            推荐
          </m-tabbar-item>
          <m-tabbar-item   id="discovery" isRouter>
            <img src="./assets/images/discovery_off.png" alt="" slot="icon-normal">
            <img src="./assets/images/discovery_on.png" alt="" slot="icon-active">
            发现
          </m-tabbar-item>
          <m-tabbar-item id="message" isRouter>
            <img src="./assets/images/message_off.png" alt="" slot="icon-normal">
            <img src="./assets/images/message_on.png" alt="" slot="icon-active">
            消息
          </m-tabbar-item>
          <m-tabbar-item id="mine" isRouter>
            <img src="./assets/images/mine_off.png" alt="" slot="icon-normal">
            <img src="./assets/images/mine_on.png" alt="" slot="icon-active">
            我的
          </m-tabbar-item>
        </m-tabbar>
      </div>
    </transition>


  </div>

</template>


<script>
  import mTabbar from 'components/tabbar'
  import mTabbarItem from 'components/tabbar-item'
  import {bus} from './bus.js'
  import vHeader from 'components/header/header'

  export default {
    name: 'app',
    components: {
      mTabbar,
      mTabbarItem,
      'v-header': vHeader
    },
    data() {
      return {
        select: "recommend",
        showApp: true,
      }
    },
    created () {
      bus.$on('show', (value) => {
        this.showApp = !value
//        console.log(this.showApp)
      })
    },
    computed: {
      showHeader() {
        if (this.select === 'mine') {
          return false;
        }
        return true
      },
      showDetail() {
        if (this.select === 'message') {
          return false
        }
        return true
      }
    },
    watch: {
      type: function (val) {
        console.log(val+'111')
      }
    }
  }
</script>

<style lang='stylus' rel="stylesheet/stylus">
  .main-leave
    transition: all .4s
    opacity: 1
  .main-enter-active
    transition: all .4s
    opacity: 1
  .main-enter, .main.leave-active
    transition: all .4s
    opacity: 0
  .header
  .content-wrapper
    display: block
    height: 800px
    background: #fff
  .tabbar-wrapper
    display: flxed
    left: 0
    bottom: 0
</style>



